(function(w, $) {
    var ts = w.TimeSlider;
    ts.EventData = function( timeSlider, aRawData ) {
        this.timeSlider = timeSlider;
        this._init( aRawData );
    };
    ts.EventData.prototype._init = function( aRawData ) {
        var self = this;
        this._aRawData = aRawData;
        this._oHash = {};
        this._aRawData.forEach(function( o ) {
            self._oHash[o.eventTypeCode + "-" + o.eventTimestamp] = o;
        });
    };
    ts.EventData.prototype.count = function() {
        return this._aRawData.length;
    };
    ts.EventData.prototype.getData = function( index ) {
        return this._aRawData[index];
    };
    ts.EventData.prototype.addData = function( aNewData ) {
        this._aLastBoundary = this._getDataBoundary( aNewData );
        this._setEventData( aNewData );
    };
    ts.EventData.prototype.getBoundary = function() {
        return this._aLastBoundary;
    };
    ts.EventData.prototype._getDataBoundary = function( aNewData ) {
        var beforeIndex = -1;
        var afterIndex = Number.MAX_SAFE_INTEGER;

        if ( this._aRawData.length === 0 ) {
            afterIndex = 0;
        } else {
            var startTime = this._aRawData[0].eventTimestamp;
            var endTime = this._aRawData[this._aRawData.length - 1].eventTimestamp;
            var len = aNewData.length;
            for ( var i = 0 ; i < len ; i++ ) {
                var time = aNewData[i].eventTimestamp;
                if ( time < startTime ) {
                    beforeIndex = i;
                } else if ( time > endTime ) {
                    afterIndex = i;
                    break;
                }
            }
        }
        return [ beforeIndex,  afterIndex ];

    };
    ts.EventData.prototype._setEventData = function( aNewData ) {
        var i;
        for ( i = this._aLastBoundary[0] ; i >= 0 ; i-- ) {
            this._aRawData.unshift( aNewData[i] );
        }
        for ( i = this._aLastBoundary[1] ; i < aNewData.length ; i++ ) {
            this._aRawData.push( aNewData[i] );
        }
    };

})(window, jQuery);
