(function(w, $) {
    var consts = {
        xAxisTicks: 5
    };
    var ts = w.TimeSlider;
    ts.PositionManager = function( options ) {
        this.width = options.width;
        this.minSliderTimeSeries = options.minSliderTimeSeries;
        this.maxSliderTimeSeries = options.maxSliderTimeSeries;
        this.startTime = options.sliderTimeSeries[0];
        this.endTime = options.sliderTimeSeries[1];
        this.timePerPoint = parseInt( ( this.endTime - this.startTime ) / this.width );
        var handleTimeSeries = options.handleTimeSeries || [];
        this.aSelectionTimeSeries = handleTimeSeries.length == 2 ? handleTimeSeries : [ this.startTime, this.endTime ];
        this.aSelectionZone = [ this.getPositionFromTime( this.aSelectionTimeSeries[0] ), this.getPositionFromTime( this.aSelectionTimeSeries[1] ) ];
        this.xAxisTicks = options.xAxisTicks || consts.xAxisTicks;
        this.selectTime = options.selectTime;
        this.selectPosition = this.getPositionFromTime( options.selectTime );
    };
    ts.PositionManager.prototype.isBeforeSliderStartTime = function( time ) {
        return time < this.startTime;
    };
    ts.PositionManager.prototype.isAfterSliderEndTime = function( time ) {
        return time > this.endTime;
    };
    ts.PositionManager.prototype.getSliderEndTime = function() {
        return this.endTime;
    };
    ts.PositionManager.prototype.setWidth = function( width ) {
        this.width = width;
        this._reset();
    };
    ts.PositionManager.prototype.getSliderEndPosition = function() {
        return this.width;
    };
    ts.PositionManager.prototype.getSliderStartTimeStr = function() {
        return this._formatDate( new Date(this.startTime) );
    };
    ts.PositionManager.prototype.getSliderEndTimeStr = function() {
        return this._formatDate( new Date(this.endTime) );
    };
    ts.PositionManager.prototype.getFullTimeStr = function( x ) {
        return this._formatDate( new Date(this.getTimeFromPosition(x)) );
    };
    ts.PositionManager.prototype._formatDate = function( d ) {
        var month = d.getMonth() + 1;
        return d.getFullYear() +"." + this._twoChipers( month )  + " " + this._twoChipers( d.getHours() ) + ":" + this._twoChipers( d.getMinutes() ) + ":" + this._twoChipers( d.getSeconds() );
    };
    ts.PositionManager.prototype._twoChipers = function( n ) {
        return n < 10 ? "0" + n : n;
    };
    ts.PositionManager.prototype.isInSelectionZone = function( x ) {
        return ( x < this.aSelectionZone[0] || this.aSelectionZone[1] < x ) ? false : true;
    };
    ts.PositionManager.prototype.isInSliderTimeSeries = function( time ) {
        return ( time < this.startTime || this.endTime < time ) ? false : true;
    };
    ts.PositionManager.prototype.getSliderTimeSeries = function() {
        return [ this.startTime, this.endTime ];
    };
    ts.PositionManager.prototype.getSelectionTimeSeries = function() {
        return this.aSelectionTimeSeries;
    };
    ts.PositionManager.prototype.getSelectionPosition = function() {
        return this.aSelectionZone;
    };
    ts.PositionManager.prototype.getSelectPosition = function() {
        return this.selectPosition;
    };
    ts.PositionManager.prototype.setSelectPosition = function( x ) {
        this.selectPosition = x;
        this.selectTime = this.getTimeFromPosition( x );
    };
    ts.PositionManager.prototype.getTimeFromPosition = function( x ) {
        return this.startTime + ( this.timePerPoint * x );
    };
    ts.PositionManager.prototype.getPositionFromTime = function( time ) {
        return parseInt( ( time - this.startTime ) / this.timePerPoint );
    };
    ts.PositionManager.prototype.calcuSelectionZone = function() {
        var defaultZone = parseInt((this.aSelectionZone[1] - this.aSelectionZone[0]) / 2);
        var zoneStart = this.selectPosition - defaultZone;
        var zoneEnd = this.selectPosition + defaultZone;
        if ( zoneStart < 0 ) {
            zoneEnd += Math.abs( zoneStart );
            zoneStart = 0;
        } else if ( zoneEnd > this.width ) {
            zoneStart -= ( zoneEnd - this.width );
            zoneEnd = this.width;
        }
        this.aSelectionTimeSeries = [ this.getTimeFromPosition( zoneStart ), this.getTimeFromPosition( zoneEnd ) ];
        this.aSelectionZone = [ zoneStart, zoneEnd ];
    };
    ts.PositionManager.prototype.getXBarPosition = function() {
        var self = this;
        var max = this.xAxisTicks + 1;
        var space = parseInt( this.width / max );
        var a = [];
        for( var i = 0 ; i < max ; i++ ) {
            if ( i === 0 ) continue;
            var x = i * space;
            a.push( {
                x: x,
                time: self.getTimeStr( x )
            });
        }
        return a;
    };
    ts.PositionManager.prototype.getTimeStr = function( x ) {
        var timeX = ( x * this.timePerPoint ) + this.startTime;
        var d = new Date( timeX );
        return this.formatTime( d.getHours() ) + ":" + this.formatTime( d.getMinutes() );
    };
    ts.PositionManager.prototype.formatTime = function( n ) {
        return n < 10 ? "0" + n : n;
    };
    ts.PositionManager.prototype.setSelectionStartPosition = function( x ) {
        this.aSelectionTimeSeries[0] = this.getTimeFromPosition(x);
        this.aSelectionZone[0] = x;
    };
    ts.PositionManager.prototype.setSelectionEndPosition = function( x ) {
        this.aSelectionTimeSeries[1] = this.getTimeFromPosition(x);
        this.aSelectionZone[1] = x;
    };
    ts.PositionManager.prototype.zoomIn = function() {
        // 선택 영역 중심으로 확대
        if( this.startTime === this.aSelectionTimeSeries[0] && this.endTime === this.aSelectionTimeSeries[1] ) return;
        var quarter = parseInt( (this.endTime - this.startTime) / 4 );
        var tempCenterTime = this.aSelectionTimeSeries[0] + ( this.aSelectionTimeSeries[1] - this.aSelectionTimeSeries[0] ) / 2;
        var tempStartTime = tempCenterTime - quarter;
        var tempEndTime = tempCenterTime + quarter;
        if ( tempEndTime - tempStartTime < this.minSliderTimeSeries ) {
            var minHalf = parseInt( this.minSliderTimeSeris / 2 );
            tempStartTime = tempCenterTime - minHalf;
            tempEndTime = tempCenterTime + minHalf;
        }
        // 기존의 선택 영역 정보가 재설정된 slider range 의 범위를 벗어나는 경우를 대비한 보정 코드
        if ( this.aSelectionTimeSeries[0] <= tempStartTime && this.aSelectionTimeSeries[1] >= tempEndTime ) {
            this.aSelectionTimeSeries[0] = tempStartTime;
            this.aSelectionTimeSeries[1] = tempEndTime;
        } else {
            var gap;
            if ( this.aSelectionTimeSeries[0] < tempStartTime ) {
                gap = tempStartTime - this.aSelectionTimeSeries[0];
                this.aSelectionTimeSeries[0]  = tempStartTime;
                if ( this.aSelectionTimeSeries[1] + gap > tempEndTime ) {
                    this.aSelectionTimeSeries[1] = tempEndTime;
                } else {
                    this.aSelectionTimeSeries[1] = this.aSelectionTimeSeries[1] + gap;
                }
            } else if ( this.aSelectionTimeSeries[1] > tempEndTime ) {
                gap = this.aSelectionTimeSeries[1] - tempEndTime;
                this.aSelectionTimeSeries[1]  = tempEndTime;
                if ( this.aSelectionTimeSeries[0] - gap < tempStartTime ) {
                    this.aSelectionTimeSeries[0] = tempStartTime;
                } else {
                    this.aSelectionTimeSeries[0] = this.aSelectionTimeSeries[0] - gap;
                }
            }
        }
        this.startTime = tempStartTime;
        this.endTime = tempEndTime;
        this._reset();
    };
    ts.PositionManager.prototype.zoomOut = function() {
        var one = this.endTime - this.startTime;
        var tempCenterTime = this.aSelectionTimeSeries[0] + ( this.aSelectionTimeSeries[1] - this.aSelectionTimeSeries[0] ) / 2;
        this.startTime = tempCenterTime - one;
        this.endTime = tempCenterTime + one;
        if ( this.endTime - this.startTime > this.maxSliderTimeSeries ) {
            var maxHalf = parseInt( this.maxSliderTimeSeries / 2 );
            this.startTime = tempCenterTime - maxHalf;
            this.endTime = tempCenterTime + maxHalf;
        }
        this._reset();
    };
    ts.PositionManager.prototype._reset = function() {
        this.timePerPoint = parseInt( ( this.endTime - this.startTime ) / this.width );
        this.selectPosition = this.getPositionFromTime( this.selectTime );
        this.aSelectionZone = [ this.getPositionFromTime( this.aSelectionTimeSeries[0] ), this.getPositionFromTime( this.aSelectionTimeSeries[1] ) ];
    };
})(window, jQuery);
